import React, {useState, useEffect} from 'react'
import {useParams, useHistory} from "react-router-dom"
import articleContent from './article-content'
import NotFoundPage from './NotFoundPage'
import CommentForm from '../components/CommentForm'
import CommentList from '../components/CommentList'
import UpvotesSection from '../components/UpvotesSection';

const ArticlePage = () => {
    const {name} = useParams()
    const history = useHistory()

 const [articleInfo, setArticleInfo] = useState({upvotes: 0, comments: []})

    const article = articleContent.find(item => item.name === name)

    useEffect(() => {
        const fetchData = async () => {
            const res = await fetch(`/api/articles/${name}`)
            const articleData = await res.json();
            setArticleInfo(articleData)
        }
        fetchData()
    }, [name])

    if(!article) return <NotFoundPage />


    return (
        <div>
            <h1>{article.title}</h1>
            <UpvotesSection articleName={name} upvotes={articleInfo.upvotes} setArticleInfo={setArticleInfo} />
            {article.content.map((para,index) => (
                <p key={index}>{para}</p>
            ))}

            <CommentList comments={articleInfo.comments} />
            <CommentForm setArticleInfo={setArticleInfo} articleName={name}/>
            <button onClick={() => history.push("/articlelist")}>Back to articles</button>
        </div>
    )
}

export default ArticlePage