import React from 'react'
import {Link} from "react-router-dom"


const ArticleList= ({article}) => {
    return (
        <div className="articlelist-cont">
            <Link className="articlelist-link" to={`/article/${article.name}`}><h3>{article.title}</h3>
            <p className="articlelist-paragraph">{article.content[0].substring(0,150)}...</p>
            </Link>
        </div>
    )
}
export default ArticleList