import React from 'react'

const CommentList = ({ comments }) => {
    return (
        <div className='comment'>
            <h3>Comments:</h3>
            { comments && comments.map(comment => (
                <div>
                    <h4>Name: {comment.username}</h4>
                    <p>Language: {comment.text}</p>
                </div>
            ))}
        </div>
    )
}

export default CommentList
